.login {
    margin: auto;
    display: flex;
    align-items: center;
    justify-items: center;
    box-sizing: border-box;
}

.login__section {
    display: flex;
    flex-direction: column;
}

.login__logo {    
    width: 38px;
    height: 38px;
}

.login__form {
    margin: 40px 0 0 0;
    width: 396px;
    display: flex;
    flex-direction: column;
}

.login__title {
    margin: 0 0 20px 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 29px;
    color: #fff;
}

.login__input-name {
    margin: 20px 0 10px 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 10px;
    font-weight: 400;
    line-height: 12px;
    color: #8B8B8B;
}

.login__input {
    padding: 0 0 0 15px;
    width: 100%;
    height: 46px;
    border-radius: 8px;
    background-color: #2F2F2F;
    border: none;
    outline: none;
    box-sizing: border-box;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
    color: #fff;
}

.login__input:focus {
    box-shadow: 0 0 0.5pt 0.5pt #4285F4;
}

.login__submit-button {
    margin: 220px 0 0 0;
    width: 100%;
    height: 45px;
    border-radius: 3px;
    background-color: #4285F4;
    border: none;
    outline: none;
    cursor: pointer;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 14px;
    font-weight: 500;
    line-height: 17px;
    color: #fff;
}

.login__submit-button:hover {
    opacity: 0.8;
    transition: ease .3s;
}

.login__task {
    margin: 16px 0 0 0;
    display: flex;
    justify-content: center;
    gap: 6px;
}

.login__task-text {
    margin: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    color: #8B8B8B;
}

.login__signup-link {
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    text-decoration: none;
    color: #4285F4;
}

.login__signup-link:hover {
    opacity: 0.7;
    transition: ease .3s;
}

.error {
    margin-top: 50px;
}

@media screen and (max-width: 767px) {
    .login {
        padding: 56px 30px 30px 30px;
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .login__section {
        width: 100%;
        align-items: center;
    }
}

@media screen and (max-width: 767px) {
    .login__form {
        margin: 40px 0 0 0;
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .login__title {
        margin: 10px 0 60px 0;
        text-align: center;
    }
}

@media screen and (max-width: 767px) {
    .login__submit-button {
        margin: 310px 0 0 0;
        font-size: 12px;
        line-height: 14px;
    }
}

@media screen and (max-width: 767px) {
    .login__task {
        margin: 14px 0 0 0;
        gap: 7px;
    }
}

@media screen and (max-width: 767px) {
    .login__task-text {
        font-size: 12px;
        line-height: 14px;
    }
}

@media screen and (max-width: 767px) {
    .login__signup-link {
        font-size: 12px;
        line-height: 14px;
    }
}
