.profile {
    padding: 70px 0 70px 0;
    height: calc(100vh - 81px);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    box-sizing: border-box;
    background-color: #202020;
}

.profile__user-data-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.profile__title {
    margin: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 24px;
    font-weight: 500;
    line-height: 29px;
    color: #fff;
}

.profile__form {
    margin: 123px 0 0 0;
    width: 410px;
}

.profile__user-name-email-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
}

.profile__user-name-email-container_type_first {
    padding: 0 0 16px 0;
    border-bottom: 1px solid rgba(66, 66, 66, 1);
}

.profile__user-name-email-container_type_second {
    padding: 16px 0 0 0;
}

.profile__user-data {
    margin: 0;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 11px;
    font-weight: 500;
    line-height: 13px;
    color: #fff;
}

.profile__input {
    max-width: 45%;
    background: none;
    border: none;
    outline: none;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 11px;
    font-weight: 500;
    line-height: 13px;
    text-align: right;
    color: #fff;
}

.profile__buttons-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 16px;
    margin-top: 30px;
}

.profile__button {
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 13px;
    font-weight: 400;
    line-height: 16px;
    text-align: center;
    color: #fff;
}

.profile__button:hover {
    opacity: 0.8;
    transition: ease .3s;
}

.profile__button_type_edit {
    max-width: 150px;
    color: #fff;
}

.profile__button_type_logout {
    max-width: 200px;
    color: #EE3465;
}

.error {
    margin-top: 20px!important;
    margin-bottom: 20px!important;
    font-style: italic;
    font-size: 14px;
}
@media screen and (max-width: 1279px) {
    .profile {
        padding: 236px 0 60px 0;
    }
}

@media screen and (max-width: 1279x) {
    .profile__form {
        margin: 96px 0 0 0;
    }
}

@media screen and (max-width: 767px) {
    .profile {
        padding: 70px 30px 40px 30px;
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .profile__user-data-container {
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .profile__form {
        margin: 80px 0 0 0;
        width: 100%;
    }
}

@media screen and (max-width: 767px) {
    .profile__button {
        font-size: 12px;
        line-height: 14px;
    }
}
