.techs {
    padding: 100px 70px 100px 70px;
    display: flex;
    flex-direction: column;
    background-color: #272727;
}

.techs__content-title {
    margin: 0;
    padding: 0 0 18px 0;
    border-bottom: 1px solid rgba(218, 218, 218, 1);
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 22px;
    font-weight: 400;
    line-height: 30px;
    color: #fff;
    letter-spacing: -0.04em;
}

.techs__container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.techs__title {
    margin: 90px 0 0 0;
    max-width: 600px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 50px;
    font-weight: 400;
    line-height: 58px;
    text-align: center;
    color: #fff;
    letter-spacing: -0.04em;
}

.techs__text {
    margin: 28px 0 0 0;
    max-width: 460px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    text-align: center;
    color: #fff;
    letter-spacing: -0.04em;
}

.techs__list {
    margin: 100px 0 0 0;
    padding: 0;
    list-style: none;
    display: flex;
    gap: 10px;
}

.techs__item {
    margin: 0;
    width: 90px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: #303030;
    border-radius: 10px;
    font-family: 'Inter', 'Arial', sans-serif;
    font-size: 14px;
    font-weight: 400;
    line-height: 17px;
    text-align: center;
    color: #fff;
    letter-spacing: -0.04em;
}

@media screen and (max-width: 1279px) {
    .techs {
        padding: 90px 50px 90px 50px;
    }
}

@media screen and (max-width: 1279px) {
    .techs__content-title {
        padding: 0 0 20px 0;;
    }
}

@media screen and (max-width: 1279px) {
    .techs__title {
        margin: 80px 0 0 0;
    }
}

@media screen and (max-width: 1279px) {
    .techs__text {
        margin: 22px 0 0 0;
        font-size: 12px;
        line-height: 18px;
    }
}

@media screen and (max-width: 1279px) {
    .techs__list {
        margin: 83px 0 0 0;
    }
}

@media screen and (max-width: 1279px) {
    .techs__item {
        width: 84px;
        height: 57px;
        font-size: 12px;
        line-height: 15px;
    }
}

@media screen and (max-width: 767px) {
    .techs__list {
        width: 70%;
        display: grid;
        grid-gap: 10px;
        grid-template-columns: repeat(auto-fill, minmax(84px, 1fr));
    }
}

@media screen and (max-width: 767px) {
    .techs__item {
       width: 100%; 
    }
}

@media screen and (max-width: 767px) {
    .techs {
        padding: 70px 18px 70px 18px;
    }
}

@media screen and (max-width: 767px) {
    .techs__content-title {
        padding: 0 0 28px 0;
        font-size: 18px;
        line-height: 22px;
    }
}

@media screen and (max-width: 767px) {
    .techs__title {
        margin: 60px 0 0 0;
        font-size: 30px;
        line-height: 36px;
    }
}

@media screen and (max-width: 767px) {
    .techs__text {
        margin: 24px 0 0 0;
        font-size: 11px;
        line-height: 16px;
        max-width: 292px;
    }
}

@media screen and (max-width: 767px) {
    .techs__list {
        margin: 50px 0 0 0;
        width: 63%;
    }
}